<template>
  <div id="edit">
    <h1>创建文章</h1>
    <h3>文章标题</h3>
    <el-input type="textarea" :autosize="{ minRows: 1, maxRows: 2}" v-model="title"></el-input>
    <p class="msg">最少10个字</p>
    <h3>文章简介</h3>
    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 3}" v-model="description"></el-input>
    <p class="msg">最少30个字</p>
    <h3>文章内容</h3>
    <el-input type="textarea" :autosize="{ minRows: 5, maxRows: 20}" v-model="content" placeholder="支持markdown语法"></el-input>
    <p class="msg">最少200个字</p>
    <p>
      是否展示到首页
      <el-switch
        v-model="atIndex"
        active-color="#13ce66"
        inactive-color="#ff4949">
      </el-switch>
    </p>
    <el-button @click="onCreate">确定</el-button>
  </div>
</template>

<script>
import blog from '@/apis/blog.js'

export default {
  data() {
    return {
      title: '',
      content: '',
      description: '',
      atIndex: false
    }
  },
  computed: {
    markdown() {
      return marked(this.rawContent)
    }
  },
  methods: {
    onCreate() {
      blog.createBlog({title: this.title, content: this.content, description: this.description,atIndex:this.atIndex}).then(res => {
        this.$message.success(res.msg)
        this.$router.push({path: `/detail/${res.data.id}`})
      })

    }
  }
}

</script>

<style src="./template.less" lang="less"></style>



